import React, { useState } from "react";
import { useFormik } from "formik";
import * as yup from "yup";
import { useNavigate } from "react-router-dom";
import icon from "../svg/proposal-icon.svg";
// import Toast from "../Toast";
import "../css/register.css";

const Register = () => {
  const [isChecked, setIsChecked] = useState(false);
  const [imgFile, setImgFile] = useState({});
  // const [toastSucces, setToastSucces] = useState(false);
  // const [toastFaild, setToastFaild] = useState(false);

  const navigate = useNavigate();

  const changeImgHandler = (e) => {
    setImgFile(e.target.files[0]);
  };

  const phoneRegExp =
    /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/;

  const formik = useFormik({
    initialValues: {
      name: "",
      lastName: "",
      userName: "",
      email: "",
      password: "",
      mobile: "",
    },
    onSubmit: (values) => {
      const data = new FormData();
      data.append("file", imgFile);
      values.imgFileData = data;
      // setErrorServer(false);
      // setLoading(true);
      // axios.post("url",values);
      // ...
      // .then(.......   setToastSucces(true))
      // .catch(()=>setToastFaild(true))
      // .finally(()=>setLoading(false))
    },
    validationSchema: yup.object({
      name: yup.string().required("    "),
      lastName: yup.string().required("     "),
      mobile: yup
        .string()
        .matches(phoneRegExp, "    ")
        .min(11, "    ")
        .max(11, "    ")
        .required("     "),
      userName: yup
        .string()
        .min(4, "    4  ")
        .max(15, "      15   ")
        .required("     "),
      email: yup
        .string()
        .email("   ")
        .required("    "),
      password: yup
        .string()
        .min(4, "   4  ")
        .required("     "),
    }),
  });

  return (
    <>
      <div className="container-register">
        <div className="form-register">
          <form onSubmit={formik.handleSubmit}>
            <div className="mb-2 register">
              <label htmlFor="name" className="form-label">
                *
              </label>
              <input
                type="name"
                className="form-control form-control-sm"
                id="name"
                {...formik.getFieldProps("name")}
              />
              {formik.touched.name && formik.errors.name ? (
                <div className="error-msg"> {formik.errors.name}</div>
              ) : null}
            </div>
            <div className="mb-2 register">
              <label htmlFor="lastName" className="form-label">
                 *
              </label>
              <input
                type="lastName"
                className="form-control form-control-sm"
                id="lastName"
                {...formik.getFieldProps("lastName")}
              />
              {formik.touched.lastName && formik.errors.lastName ? (
                <div className="error-msg"> {formik.errors.lastName}</div>
              ) : null}
            </div>
            <div className="mb-2 register">
              <label htmlFor="mobile" className="form-label">
                 *
              </label>
              <input
                type="mobile"
                className="form-control form-control-sm"
                id="mobile"
                {...formik.getFieldProps("mobile")}
              />
              {formik.touched.mobile && formik.errors.mobile ? (
                <div className="error-msg"> {formik.errors.mobile}</div>
              ) : null}
            </div>
            <div className="mb-2 register">
              <label htmlFor="userName" className="form-label">
                 *
              </label>
              <input
                type="userName"
                className="form-control form-control-sm"
                id="userName"
                {...formik.getFieldProps("userName")}
              />
              {formik.touched.userName && formik.errors.userName ? (
                <div className="error-msg"> {formik.errors.userName}</div>
              ) : null}
            </div>
            <div className="mb-2 register">
              <label htmlFor="email" className="form-label">
                 *
              </label>
              <input
                type="email"
                className="form-control form-control-sm"
                id="email"
                {...formik.getFieldProps("email")}
              />
              {formik.touched.email && formik.errors.email ? (
                <div className="error-msg"> {formik.errors.email}</div>
              ) : null}
            </div>
            <div className="mb-2 register">
              <label htmlFor="password" className="form-label">
                 *
              </label>
              <input
                type="password"
                className="form-control form-control-sm"
                id="password"
                {...formik.getFieldProps("password")}
              />
              {formik.touched.password && formik.errors.password ? (
                <div className="error-msg"> {formik.errors.password}</div>
              ) : null}
            </div>
            <div className="msg-send">
              <small>       </small>
            </div>
            <div className="input-group">
              <input
                type="file"
                className="form-control form-control-sm"
                id="inputGroupFile04"
                onChange={changeImgHandler}
              />
            </div>
            <div
              className="modal fade"
              id="staticBackdrop"
              data-bs-backdrop="static"
              data-bs-keyboard="false"
              tabindex="-1"
              aria-labelledby="staticBackdropLabel"
              aria-hidden="true"
            >
              <div className="modal-dialog modal-dialog-scrollable">
                <div className="modal-content">
                  <div className="modal-header">
                    <button
                      type="button"
                      className="btn-close"
                      data-bs-dismiss="modal"
                      aria-label="Close"
                    ></button>
                  </div>
                  <div className="modal-body">
                    <h1>        </h1>
                              
                                
                    :          
                               
                           .  
                             
                      : 1-      
                     . 2-       
                    .         
                              
                     .        
                             
                    .         
                               
                         (   
                         )  . 
                              
                               
                               
                     .          
                        .       
                               
                               
                              .
                              
                             .    
                       .       
                                .
                             
                         .    
                              
                      .         
                               
                               
                              
                    .          
                               
                    .            
                        .       
                               .
                                
                         .       
                           .   :
                             48    
                            91004025-021  
                     info@homais.com      (  
                        )     
                               
                       .     48   
                     .(       
                              
                              
                      )       
                               
                              .
                       7         
                                
                      .         
                      .       
                     :         
                        .         
                        .       
                        500000        
                        .       
                              . 
                               .
                              24  
                             
                    91004025-021    info@homais.com    
                     (       )   
                               
                              
                    .        24    
                      .
                  </div>
                  <div className="modal-footer">
                    <button
                      type="button"
                      className="btn btn-secondary"
                      data-bs-dismiss="modal"
                    >
                      
                    </button>
                  </div>
                </div>
              </div>
            </div>

            <div className="m-2 form-check">
              <input
                defaultChecked={isChecked}
                onChange={() => {
                  setIsChecked(!isChecked);
                }}
                type="checkbox"
                className="form-check-input"
                id="remember"
              />
              <label className="form-check-label" htmlFor="remember">
                <button
                  id="modal-btn"
                  type="button"
                  className="btn btn-primary"
                  data-bs-toggle="modal"
                  data-bs-target="#staticBackdrop"
                >
                    
                </button>
                <span className="acc-msg">  .</span>
              </label>
            </div>
            <div className="submit-container">
              <button
                disabled={!isChecked}
                type="submit"
                className="btn register-btn"
              >
                 
              </button>
              <button
                className="go-home-reg"
                onClick={() => {
                  navigate("/login");
                }}
              >
                 
              </button>
            </div>
          </form>
        </div>
        <div className="img-icon">
          <img src={icon} alt="icon" />
        </div>
      </div>
      {/* {toastSucces && (
        <Toast type="success" msg="      " />
      )}
      {toastFaild && (
        <Toast type="warn" msg="       " />
      )} */}
    </>
  );
};

export default Register;
